//
/**
 * @param {Object} fn 
 * @param {Object} wait 
 * @param {Object} immediate  true  false 
 */
export function debounce(fn, wait = 500, immediate = false) {
  let timeout; // 
  return function(...args) {
    let context = this;
    if (timeout) clearTimeout(timeout); // timeout

    if (immediate) { // 
      let callNow = !timeout; // true
      // timeoutwaittimeout
      timeout = setTimeout(function() {
        timeout = null;
      }, wait);
      // true, 
      if (callNow) fn.apply(context, args);
    } else { // 
      timeout = setTimeout(function() {
        fn.apply(context, args);
      }, wait);
    }
  };
}

//
/**
 * @param {object} date 
 * 
 */
export function dateFormat(date, format = 'yyyy-MM-dd hh:mm:ss') {
  if (typeof date === 'string') {
    return date
  }
  let d = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds(),
    'q+': Math.floor((date.getMonth() + 3) / 3),
    s: date.getMilliseconds()

  }
  if (/(y+)/.test(format)) {
    format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));

  }
  for (let i in d) {
    if (new RegExp('(' + i + ')').test(format)) {
      format = format.replace(RegExp.$1, RegExp.$1.length === 1 ? d[i] : ('00' + d[i]).substr(('' + d[i])
        .length));
    }
  }
  return format;
}


//url
export function getImgSrc(richtext, num = 3) {
  let imgList = [];
  richtext.replace(/<img [^>]*src=['"]([^'"]+)[^>]*>/g, (match, capture) => {
    imgList.push(capture);
  });
  imgList = imgList.slice(0, num)
  return imgList;
}

//+
export function getProvince() {
  return new Promise((resolve, reject) => {
    let historyProvince = uni.getStorageSync("historyProvince")
    if (historyProvince) {
      //
      if ((Date.now() - historyProvince.time) > 1000 * 60 * 60) {
        // console.log("");
        getIp().then(res => {
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      } else {
        resolve(historyProvince.province)
        // console.log("", historyProvince.province)
      }

    } else {
      getIp().then(res => {
        resolve(res)
      }).catch(err => {
        reject(err)
      })
    }
  })

}


// 

function getIp() {
  return new Promise((resolve, reject) => {
    uni.request({

      url: "https://restapi.amap.com/v3/ip?key=f55ac7c6a06cce856428e551f0acf0fc",
      success: res => {
        // console.log("")
        let str = ""
        if (typeof(res.data.province) == "string") {
          str = res.data.province
        } else {
          str = ''
        }
        let obj = {
          province: str,
          time: Date.now()
        }
        uni.setStorageSync("historyProvince", obj)
        resolve(str); //
      },
      fail: err => {
        reject(err)
      }
    })
  })
}
